* {
  padding: 0;
  margin: 0;
}

a {
  color: rgb(20, 13, 13);
}
a:hover {
  color: #714949;
}
a:active {
  color: blue;
}
a:visited {
  color: green;
}

.header a {
  color: rgb(20, 13, 13);
  background: rgb(207, 230, 8);
}
.header a:hover {
  color: #714949;
}
.header a:active {
  color: blue;
}
.header a:visited {
  color: green;
}

a {
  font-size: 1.1em;
  font-weight: bold;
  font-family: Arial, Helvetica, sans-serif;
  color: red;
}

.block:not(.first) {
  background: #000000;
  height: 200px;
  margin-bottom: 20px;
  width: 30%;
  border: 4px solid rgb(108, 16, 212);
  color: #ccc;
}

.first {
  background: #000000;
  height: 200px;
  margin-bottom: 20px;
  width: 300px;
}

#grid {
  float: left;
  width: 100%;
}
#grid > div {
  float: left;
  background: #ce3d3d;
  margin-right: 5%;
  margin-bottom: 5%;
  height: 150px;
  width: 100%;
  margin-right: 0;
}
#grid > div:nth-child(6n) {
  margin-right: 0;
}

.some_class-24 {
  width: 74px;
  height: 74px;
}

.some_class-25 {
  width: 75px;
  height: 75px;
}

.some_class-26 {
  width: 76px;
  height: 76px;
}

.some_class-27 {
  width: 77px;
  height: 77px;
}

.some_class-28 {
  width: 78px;
  height: 78px;
}

#central_block {
  padding: 30px;
}
#central_block ul {
  display: inline-block;
  color: rgb(190, 221, 13);
}
#central_block li {
  float: left;
  width: 24%;
}
#central_block a {
  font-size: 1.1em;
  font-weight: bold;
  font-family: Arial, Helvetica, sans-serif;
  color: blue;
}
#central_block a:hover {
  color: red;
}

#central_block > ul {
  list-style: none;
}